body,
html {
  font-family: Toss Product Sans, Tossface, -apple-system, BlinkMacSystemFont,
    Bazier Square, Noto Sans KR, Segoe UI, Apple SD Gothic Neo, Roboto,
    Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji,
    Segoe UI Symbol, Noto Color Emoji;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  word-break: keep-all;
  word-wrap: break-word;
}

*,
:after,
:before {
  box-sizing: border-box;
}

button:disabled,
input:disabled {
  cursor: not-allowed;
}

button,
input,
textarea {
  font-family: Toss Product Sans, -apple-system, BlinkMacSystemFont,
    Bazier Square, Noto Sans KR, Segoe UI, Apple SD Gothic Neo, Roboto,
    Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji,
    Segoe UI Symbol, Noto Color Emoji;
}

.color--grey800 {
  color: #333d4b;
  color: var(--grey800);
}

.color--grey700 {
  color: #4e5968;
  color: var(--grey700);
}

.color--grey600 {
  color: #6b7684;
  color: var(--grey600);
}

.color--grey500 {
  color: #8b95a1;
  color: var(--grey500);
}

.color--blue500 {
  color: #3182f6;
  color: var(--blue500);
}

.color--blue700 {
  color: #1b64da;
  color: var(--blue700);
}

.bg--white {
  background-color: #fff;
  background-color: var(--white);
}

.bg--grey100 {
  background-color: #f2f4f6;
  background-color: var(--grey100);
}

.bg--greyOpacity100 {
  background-color: rgba(2, 32, 71, 0.05);
  background-color: var(--greyOpacity100);
}

.bg--greyOpacity200 {
  background-color: rgba(0, 27, 55, 0.1);
  background-color: var(--greyOpacity200);
}

.bg--blue50 {
  background-color: #e8f3ff;
  background-color: var(--blue50);
}

:root {
  /* --grain :'data:image/png;base64,' */
  --padding-base-vertical: 11px;
  --padding-base-horizontal: 16px;
  --padding-t-vertical: 4px;
  --padding-t-horizontal: 8px;
  --padding-s-vertical: 7px;
  --padding-s-horizontal: 12px;
  --padding-l-vertical: 11px;
  --padding-l-horizontal: 22px;
  --padding-xl-vertical: 18px;
  --padding-xl-horizontal: 24px;
  --padding-container-base: 48px;
}

.padding--base {
  padding: 11px 16px;
  padding: var(--padding-base-vertical) var(--padding-base-horizontal);
}

.padding--t {
  padding: 4px 8px;
  padding: var(--padding-t-vertical) var(--padding-t-horizontal);
}

.padding--s {
  padding: 7px 12px;
  padding: var(--padding-s-vertical) var(--padding-s-horizontal);
}

.padding--l {
  padding: 11px 22px;
  padding: var(--padding-l-vertical) var(--padding-l-horizontal);
}

.padding--xl {
  padding: 18px 24px;
  padding: var(--padding-xl-vertical) var(--padding-xl-horizontal);
}

:root {
  --radius-s: 4px;
  --radius-m: 8px;
  --radius-l: 16px;
  --radius-xl: 20px;
  --radius-xxl: 24px;
}

.radius--s {
  border-radius: 4px;
  border-radius: var(--radius-s);
}

.radius--m {
  border-radius: 8px;
  border-radius: var(--radius-m);
}

.radius--l {
  border-radius: 16px;
  border-radius: var(--radius-l);
}

.radius--xl {
  border-radius: 20px;
  border-radius: var(--radius-xl);
}

.radius--xxl {
  border-radius: 24px;
  border-radius: var(--radius-xxl);
}

:root {
  --shadow-s: 0 0 4px 0 var(--greyOpacity100),
    0 4px 16px 0 var(--greyOpacity100);
  --shadow-m: 0 8px 16px 0 var(--greyOpacity200),
    0 4px 8px 0 var(--greyOpacity100);
  --shadow-l: 0 24px 40px 0 var(--greyOpacity50),
    0 16px 24px 0 var(--greyOpacity200), 0 0 8px 0 var(--greyOpacity100);
}

.shadow--s {
  box-shadow: 0 0 4px 0 rgba(2, 32, 71, 0.05),
    0 4px 16px 0 rgba(2, 32, 71, 0.05);
  box-shadow: var(--shadow-s);
}

.shadow--m {
  box-shadow: 0 8px 16px 0 rgba(0, 27, 55, 0.1),
    0 4px 8px 0 rgba(2, 32, 71, 0.05);
  box-shadow: var(--shadow-m);
}

.shadow--l {
  box-shadow: 0 24px 40px 0 rgba(0, 23, 51, 0.02),
    0 16px 24px 0 rgba(0, 27, 55, 0.1), 0 0 8px 0 rgba(2, 32, 71, 0.05);
  box-shadow: var(--shadow-l);
}

.text--left {
  text-align: left;
}

.text--right {
  text-align: right;
}

.text--center {
  text-align: center;
}

.text--justify {
  text-align: justify;
}

:root {
  --line-height-base: 1.6;
  --line-height-adjust: 1.3;
  --font-size-h1: 56px;
  --font-size-h2: 48px;
  --font-size-h3: 36px;
  --font-size-h4: 32px;
  --font-size-h5: 24px;
  --font-size-h6: 20px;
  --font-size-h7: 17px;
  --font-size-p: 15px;
  --font-size-sm: 13px;
  --font-size-small: 13px;
  --font-size-xsmall: 11px;
  --font-weight-bold: bold;
  --font-weight-semibold: 600;
  --font-weight-medium: 500;
  --font-weight-regular: normal;
}

.typography {
  margin: 0;
  padding: 0;
}

.typography--h1,
.typography--h2,
.typography--h3,
.typography--h4 {
  line-height: 1.3;
  line-height: var(--line-height-adjust);
}

.typography--h1 {
  font-size: 56px;
  font-size: var(--font-size-h1);
}

.typography--h2 {
  font-size: 48px;
  font-size: var(--font-size-h2);
}

.typography--h3 {
  font-size: 36px;
  font-size: var(--font-size-h3);
}

.typography--h4 {
  font-size: 32px;
  font-size: var(--font-size-h4);
}

.typography--h5,
.typography--h6,
.typography--h7,
.typography--p,
.typography--sm,
.typography--small,
.typography--xsmall {
  line-height: 1.6;
  line-height: var(--line-height-base);
}

.typography--h5 {
  font-size: 24px;
  font-size: var(--font-size-h5);
}

.typography--h6 {
  font-size: 20px;
  font-size: var(--font-size-h6);
}

.typography--h7 {
  font-size: 17px;
  font-size: var(--font-size-h7);
}

.typography--p {
  font-size: 15px;
  font-size: var(--font-size-p);
}

.typography--sm {
  font-size: 13px;
  font-size: var(--font-size-sm);
}

.typography--small {
  font-size: 13px;
  font-size: var(--font-size-small);
}

.typography--xsmall {
  font-size: 11px;
  font-size: var(--font-size-xsmall);
}

.typography--bold {
  font-weight: 700;
}

.typography--semibold {
  font-weight: 600;
}

.typography--medium {
  font-weight: 500;
}

.typography--regular {
  font-weight: 400;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.reset-button {
  background: none;
  border: 0;
  padding: 0;
  outline: none;
  cursor: pointer;
}

.flex-center-all {
  display: flex;
  justify-content: center;
  align-items: center;
}

.reset-anchor {
  text-decoration: none;
}

@media (max-width: 639px) {
  .desktop-only {
    display: none !important;
  }
}

@media (min-width: 640px) {
  .mobile-only {
    display: none !important;
  }
}

:root {
  --nav-index: 50;
  --dimmer-index: 100;
  --drawer-index: 110;
  --toast-index: 500;
}

.p-accordion {
  width: 100%;
}

.p-accordion__item--with-bottom-divider
  + .p-accordion__item--with-bottom-divider:before {
  content: "";
  border-top: 1px solid rgba(0, 27, 55, 0.1);
  border-top: 1px solid var(--greyOpacity200);
  display: block;
}

.p-accordion__item {
  width: 100%;
  overflow: hidden;
  background: #fff;
}

.p-accordion__item[focus-within] {
  outline-offset: -2px;
  outline: 2px solid #64a8ff;
  outline: 2px solid var(--blue300);
}

.p-accordion__item:focus-within {
  outline-offset: -2px;
  outline: 2px solid #64a8ff;
  outline: 2px solid var(--blue300);
}

.p-accordion__item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.p-accordion__item-arrow-icon {
  transition: transform 335ms ease;
  pointer-events: none;
  transform: rotate(0deg);
}

.p-accordion__item-arrow-icon.p-accordion__item-arrow-icon--reverse {
  transform: rotate(180deg);
}

.p-accordion__item-description {
  width: 100%;
  display: block;
}

.p-accordion__item-description--small {
  padding: 6px 26px 19px 12px;
}

.p-accordion__item-description--medium {
  padding: 8px 30px 22px 16px;
}

.p-accordion__item-description--large {
  padding: 10px 30px 24px 22px;
}

.p-add-option-form {
  display: flex;
  flex-direction: row;
}

.p-add-option-form__input {
  margin-right: 8px;
}

.p-add-option-form__opener {
  width: 100%;
}

.p-agreement__title.p-agreement__title--spacing-small {
  margin-bottom: 6px;
}

.p-agreement__title.p-agreement__title--spacing-medium {
  margin-bottom: 14px;
}

.p-agreement__item.p-agreement__item--font-weight-regular
  .checkable__label-text {
  font-weight: 400;
  font-weight: var(--font-weight-regular);
}

.p-agreement__item.p-agreement__item--font-weight-bold .checkable__label-text {
  font-weight: 700;
