.p-tab__item:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  transform: translate(-50%, -50%);
  border-radius: 8px;
  background-color: rgba(2, 32, 71, 0.05);
  background-color: var(--greyOpacity100);
  opacity: 0;
  transition: opacity 0.2s ease;
}

