  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.p-tab__indicator {
  background-color: #191f28;
  background-color: var(--grey900);
  border-radius: 1px;
  height: 2px;
}

.p-tab:after,
.p-tab__indicator {
  content: "";
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -1px;
